.container-fluid {
    padding: 0;
    margin: 0;
}

.overlay__section {
    color: #021539;
    // border: 1px solid rgb(126, 112, 112);
    .image__hidden {
        // border: 1px solid white;
        // width: 80%;
        img {
            width: 100%;
            height: 100%;
        }
    }
    .overlay__box {
        // border: 1px solid blue;
        clip-path: polygon(0 30%, 100% 0, 100% 100%, 0% 100%);
        background-color: #dff1ff;
        height: auto;
        padding-top: 200px;
        margin-top: -350px;
        @media screen and (max-width: 1200px) {
            margin-top: -250px;
            padding-top: 150px;
        }
        @media screen and (max-width: 768px) {
            // margin-top: -300px;
            // padding-top: 100px;
        }
        @media screen and (max-width: 480px) {
            clip-path: polygon(0 15%, 100% 0, 100% 100%, 0% 100%);
            margin-top: -150px;
            padding-top: 50px;
        }
        .overlay__inner {
            // border: 1px solid red;
            width: 80%;
            margin: auto;
            padding: 1em;
            @media screen and (max-width: 480px) {
                width: 100%;
            }
            .host-session {
                // border: 1px solid black;
                .btn {
                    background: #39A2F7;
                    background: -webkit-linear-gradient(right, #39A2F7, #A975FF);
                    background: -moz-linear-gradient(right, #39A2F7, #A975FF);
                    background: linear-gradient(to left, #39A2F7, #A975FF);
                    color: #fff;
                    border-radius: 20px;
                }
            }
            .updates {
                // border: 1px solid black;
                .app {
                    // border: 1px solid green;
                    display: flex;
                    .box {
                        cursor: pointer;
                        // border: 1px solid black;
                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }
                }
            }
        }
    }
}